#name {
  position: relative;
  font-size: 25px;
  font-family: "Anton";
  padding-top: 50px;
  font-weight: 100;
  letter-spacing: 5px;
  color: orange;
  list-style-type: none;
  text-transform: uppercase;
  line-height: normal;
  padding-bottom: 10px;
  
}
#photo {
  position: relative;
  float: right;
  border: 3px solid orange;
  border-radius: 5px;
