#socialmedia {

  box-sizing: border-box;
  background-color: orange;
  margin: 20px;
  text-align: center;
  padding: 40px;
  font-size: 50px;
  position: relative;
  overflow: auto;
}
#socialmedia i {
  color: black;
  position: relative;
}
@media screen and (max-width:700px) {
  #socialmedia i {
    color: black;
    position: relative;
    margin-top: 20px;
  }
  #socialmedia {
    padding: 15px
  }
}

#backk {
  position: relative;
  color: black;
  text-align: center;
  margin: 30px;

  margin-bottom: 100px;
}
#backk a {
  background-color: orange;
  padding: 20px;
  font-size: 30px;
  color: black;
  font-family: "Anton";
}
#mernapp {
  color: orange;
  text-align: center;
  font-size: 40px;
  line-height: normal;
  margin-top: 70px;
  margin-bottom: 30px;
}
#cat {
  text-align: center;
  padding-bottom: 10px;
}
#catphoto {
  max-width: 130px;
  position: relative;
}
#meow {
  color: white;
  line-height: 2rem;
  text-align: center;
  font-size: 20px;
  margin-bottom: 100px;
}